// Fonts
@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,600");
@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700");
@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import 'mixins';
@import 'variables';
@import 'helpers';



div {
  // border: 0.5px solid grey
  box-sizing: border-box; }

body, html {
  font: 14px/20px "Lato", Arial, sans-serif;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #007991, #78ffd6); }// linear-gradient(45deg, #e66465, #9198e5)

main {
  border-radius: 10px;
  width: 800px;
  border: 1px solid grey;
  margin: 20px auto;
  height: 700px; }

.navigation {
  text-align: center;
  margin-top: 20px;
  a {
      margin: 20px;
      color: $lightGrey; } }

.error-message {
  text-align: center;
  font-size: 40px;
  margin-top: 100px; }
.links {
  text-align: center;
  a {
      color: $lightGrey;
      margin: 0 20px; } }

@import 'components/chat';
@import 'components/register';
@import 'components/login';
